.about {
    padding: 20px;
    background-color: #f4f4f4;
  }
  
  .about-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .about-text {
    flex: 1;
    margin-right: 20px;
  }
  
  .typing-effect {
    overflow: hidden; /* Ensures the text doesn't overflow */
    white-space: nowrap; /* Keeps text on one line */
    animation: typing 5s 
  }
  
  @keyframes typing {
    from { width: 0; }
    to { width: 30%; }
  }
  
  @keyframes blink-caret {
    from, to { border-color: transparent; }
    50% { border-color: #4caf50; }
  }
  
  .about-text h2 {
    margin-bottom: 10px;
  }
  
  .about-text h3 {
    margin-bottom: 5px;
    font-weight: bold; /* Added bold style */
  }
  
  .about-text h4 {
    margin-bottom: 15px;
    font-weight: normal; /* Style for Python Developer heading */
  }
  
  .about-text p {
    margin-bottom: 20px;
  }
  
  .cv-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: #4caf50;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    font-weight: bold;
  }
  
  .cv-button:hover {
    background-color: #45a049;
  }
  
  .about-photo {
    flex: 1;
    display: flex;
    justify-content: center;
  }
  
  .about-photo img {
    max-width: 100%;
    border-radius: 50%;
    height: auto;
  }
  