.skills {
    padding: 20px;
    background-color: #f4f4f4;
  }
  
  .skills h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .skill-item {
    margin-bottom: 15px;
  }
  
  .skill-name {
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .skill-bar-container {
    background-color: #ddd;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
  }
  
  .skill-bar {
    height: 25px;
    background-color: #4caf50;
    color: #fff;
    text-align: center;
    line-height: 25px;
    border-radius: 5px;
    position: relative;
  }
  
  .skill-percentage {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 0.9em;
  }
  