.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: #333;
    color: #fff;
    animation: slideInDown 1s ease-out;
    position: relative;
  }
  
  @keyframes slideInDown {
    from {
      transform: translateY(-100%);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  .logo {
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  nav ul {
    list-style: none;
    display: flex;
    gap: 1rem;
  }
  
  nav a {
    color: #fff;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  nav a:hover {
    color: #ff6347;
    text-decoration: underline;
  }
  
  .nav-toggle {
    display: none;
    background: none;
    border: none;
    font-size: 1.5rem;
    color: #fff;
    cursor: pointer;
  }
  
  .nav {
    display: flex;
  }
  
  .nav ul {
    display: flex;
  }
  
  @media (max-width: 768px) {
    .nav {
      position: absolute;
      top: 100%;
      right: 0;
      background-color: #333;
      width: 200px;
      display: none;
      flex-direction: column;
      gap: 0;
    }
  
    .nav.open {
      display: flex;
    }
  
    .nav ul {
      display: block;
    }
  
    .nav li {
      padding: 1rem;
    }
  
    .nav-toggle {
      display: block;
    }
  }
  